import React from "react"
import {Link} from "react-router-dom"
import "../styles/books.css";

function Books(props) {

    // Obj bookFinal de las variables reelevantes
    // const { name, audio_url, narrator, free_time, duration, sinopsis, slug, photos: {image_url}, author: {name: author_name} } = booksObj
    
  
    const books = props.data.map(book => 
        <Link key={book.id} to={`/books/${book.slug}`}>
            <img src={`https://streamreaders.com${book.photos[0].image_url}`} />
        </Link>
    )   
       
    return (
        <div className="booksCont">{books}</div>
    )

}


