import React from 'react'
import IconCard from './IconCard'
import '../styles/iconSection.css'


function TextSection(){
    return(
        <section className="icon-section">
            <h2>¿POR QUÉ STREAM READERS?</h2>
            <div className="icon-container">
                <IconCard src={require("../assets/ICONO LEER.png")} alt="leer" text="¿Te has imaginado alguna vez escuchando un libro? Entra en el mundo de la literatura de una forma diferente." />
                <IconCard src={require("../assets/icono auris.png")} alt="auris" text="Prepárate para experimentar a través de distintos efectos de sonido, la sensación de estar vivenciando grandes obras literarias." />
                <IconCard src={require("../assets/Icono Play.png")} alt="play" text="La música y la ambientación harán de la experiencia algo mucho más enriquecedor. Vive la experiencia Stream Readers." />
            </div>
        </section>
    )
}

export default TextSection
