import React from 'react'
import '../styles/textSection.css'


function TextSection(){
    return(
        <section className="text-section">
            <div>
                <p>Ahora puedes escuchar los mejores clásicos en cualquier momento, mientras cocinas, viajas o corres.</p>
                <p><span className="orange-text">Bienvenido a Stream Readers</span></p>
            </div>
        </section>
    )
}

export default TextSection