import React from "react";
import "../styles/carouselTapas.css";
import InfiniteCarousel from "react-leaf-carousel";

function CarouselTapas() {
  return (
    <section className="carouselTapas">
    <InfiniteCarousel 
      breakpoints={[
        {
          breakpoint: 500,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
          },
        },
        {
          breakpoint: 768,
          settings: {
            slidesToShow: 5,
            slidesToScroll: 5,
          },
        },
      ]}
      dots={false}
      showSides={true}
      sidesOpacity={1}
      sideSize={0.1}
      slidesToScroll={5}
      slidesToShow={5}
      scrollOnDevice={false}
      arrows={false}
      swipe={false}
      slidesSpacing={20}
      animationDuration={6000}
      autoCycle={true}
      cycleInterval={6050}
      lazyLoad={true}
      responsive={true}
    >
      <div>
        <img src={require("../assets/tapas/el_anticristo.jpg")} alt="anticristo" />
      </div>
      <div>
        <img src={require("../assets/tapas/el_corazon_delator.jpg")} alt="corazon" />
      </div>
      <div>
        <img src={require("../assets/tapas/la-caida.jpg")} alt="la_caida_de_la_casa_usher" />
      </div>
      <div>
        <img src={require("../assets/tapas/ruinas_circulares_nuevo2.jpg")} alt="ruinas_circulares" />
      </div>
      <div>
        <img src={require("../assets/tapas/el_cuervo.jpg")} alt="el_cuervo" />
      </div>
      <div>
        <img src={require("../assets/tapas/axolotl_nuevo2.jpg")} alt="axolotl" />
      </div>
      <div>
        <img src={require("../assets/tapas/senorita_cora_nuevo2.jpg")} alt="senorita_cora" />
      </div>
      <div>
        <img src={require("../assets/tapas/autopista_del_sur_nuevo2.jpg")} alt="autopista_del_sur" />
      </div>
      <div>
        <img src={require("../assets/tapas/el-arte.jpg")} alt="el-arte" />
      </div>
      <div>
        <img src={require("../assets/tapas/el-kybalion.jpg")} alt="el-kybalion" />
      </div>
      <div>
        <img src={require("../assets/tapas/el-lobo.jpg")} alt="el-lobo" />
      </div>
      <div>
        <img src={require("../assets/tapas/el-principito.jpg")} alt="el-principito" />
      </div>
      <div>
        <img src={require("../assets/tapas/gato-negro.jpg")} alt="gato-negro" />
      </div>
      <div>
        <img src={require("../assets/tapas/la-metamorfosis.jpg")} alt="la-metamorfosis" />
      </div>
      <div>
        <img src={require("../assets/tapas/siddartha.jpg")} alt="siddartha" />
      </div>
      
      
    </InfiniteCarousel>

    </section>  
  );
}

export default CarouselTapas;

