import React from 'react'
import {Link} from "react-router-dom"
import '../styles/header.css'


function Header(props){
    return(
        <div className="header">
            <img src={require('../assets/logo.png')} alt="logo"/>

            <ul className="menu">
                <li><Link to='/' className="link">BIBLIOTECA</Link></li>
                <li><Link to='/suscribete' className="link">SUSCRIBETE</Link></li>
                <li><Link to='/nosotros' className="link">NOSOTROS</Link></li>
                <li><Link to='/stream-readers' className="link">¿POR QUÉ STREAM READERS?</Link></li>
                <li><button onClick={props.toggle}>CONTACTO</button></li>
            </ul>
        </div>
    )
}

export default Header