import React from 'react'
import '../styles/iconCard.css'


function IconCard(props){
    return(
        
        <div className="icon-card">
            <div className="icon-cont">
                <img src={props.src} alt={props.alt} />
            </div>
            <p>{props.text}</p>
        </div>
        
    )
}

export default IconCard