import React from 'react'
import '../styles/footer.css'


function Footer(){
    return(
        <footer>
            <div className="social">
                <a href="mailto:contacto@streamreaders.com"><img src={require("../assets/mail-icon.png")} alt="email" className="social-icons" /></a>
                <a href="https://www.instagram.com/streamreaders/"><img src={require("../assets/ig-icon.png")} alt="instagram" className="social-icons" /></a>
                <a href="https://www.facebook.com/Streamreaders/"><img src={require("../assets/facebook-icon.png")} alt="facebook" className="social-icons" /></a>
            </div>
            <div className="copy-container">
                <p className="copyright">{`Stream Readers © Copyright ${new Date().getFullYear()}`}</p>
                
            </div>
        </footer>
    )
}

export default Footer