import React, {useState, useEffect} from 'react';
import {Switch, Route} from "react-router-dom"
import './styles/app.css'
import Hero from './components/Hero'
import TextSection from './components/TextSection'
import IconSection from './components/IconSection'
import CarouselTapas from './components/CarouselTapas'
import IconSectionLanding from './components/IconSectionLanding'
import TextSectionLanding from './components/TextSectionLanding'
import ReviewsSection from './components/ReviewsSection'
import PricingSection from './components/PricingSection'
import FormSection from './components/FormSection'
import Footer from './components/Footer'
import Books from './components/Books'


function App() {
  const [booksData, setBooksData] = useState([])
  const [isLoggedin, setisLoogedin] = useState(false)
    

  useEffect(() => {
          const getBooks = async () => {      
            const booksPromise = await fetch("https://streamreaders.com/api/books")

            const books = await booksPromise.json()

            setBooksData(books)

          }
          getBooks()
      }
  , [])


  return (
    <div>
      <Switch>
        <Route exact path="/">
          <Hero heroClass="hero home" page="home" />
          <TextSection />
          <IconSection />
          <Books data={booksData}/>
          <FormSection />
          <Footer />

        </Route>

        <Route exact path="/suscribete">
          <Hero heroClass="hero landing" page="landing"/>
          <IconSectionLanding />
          <CarouselTapas />
          <TextSectionLanding />
          <ReviewsSection />
          <PricingSection />
          <FormSection />
          <Footer />
        </Route>

        <Route exact path="/nosotros">
          <Hero heroClass="hero nosotros" page="nosotros"/>
          <FormSection />
          <Footer />
        </Route>

        <Route exact path="/stream-readers">
          <Hero heroClass="hero porque" page="porque"/>
          <FormSection />
          <Footer />
        </Route>

        {booksData.map( book => 
          <Route path={`/books/${book.slug}`}>
            <Hero
              heroClass="hero book"
              page="book"
              name={book.name} 
              audio_url={book.audio_url}  
              narrator={book.narrator} 
              free_time={book.free_time} 
              duration={book.duration}
              sinopsis={book.sinopsis}
              slug={book.slug}
              image_url={book.photos[0].image_url}
              author_name={book.author.name}
              isLoggedin={isLoggedin}
            />
            <Footer />
          </Route>
        )}

      </Switch>
    </div>
  );
}

export default App;
