import React from 'react'
import '../styles/iconCardSmall.css'


function IconCardSmall(props){
    return(
        
        <div className="icon-card-small">
            <div className="icon-cont">
                <img src={props.src} alt={props.alt} />
            </div>
            <p>{props.text}</p>
        </div>
        
    )
}

export default IconCardSmall