import React, {useState} from 'react'
import Header from './Header'
import HeroElLanding from './HeroElLanding'
import HeroElHome from './HeroElHome'
import HeroElNosotros from './HeroElNosotros'
import HeroElPorque from './HeroElPorque'
import BookPage from './BookPage'
import Contact from './Contact'
import '../styles/hero.css'


function Hero(props){
    const [modalIsOpen, setModalIsOpen] = useState(false)
    function toggleModal(){
        setModalIsOpen(prevState => !prevState)    
    }

    return(
        <div className={props.heroClass}>
            <Header toggle={toggleModal}/>            
            {props.page === "home"
                ? <HeroElHome />
                : props.page === "nosotros"
                    ? <HeroElNosotros /> 
                    : props.page === "landing"
                        ? <HeroElLanding /> 
                        : props.page === "porque" 
                            ? <HeroElPorque /> 
                        : <BookPage 
                            name={props.name} 
                            audio_url={props.audio_url}
                            narrator={props.narrator} 
                            free_time={props.free_time} 
                            duration={props.duration}
                            sinopsis={props.sinopsis}
                            slug={props.slug}
                            image_url={props.image_url}
                            author_name={props.author_name}
                            isLoogedin={props.isLoogedin}
                        />
        }
            <Contact isOpen={modalIsOpen} toggle={toggleModal}/>
        </div>
    )
}


export default Hero