
import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import plyr from "plyr";
import "plyr/dist/plyr.css";
import '../styles/audioPlayer.css'

const Plyr = (props) => {
  const [ currentTime, setCurrentTime ] = useState(0)
  
  let player

   useEffect(() => {
    player = new plyr(".js-plyr", props.options);
    
    
    const sources = {
      type: "audio",
      sources: [
        {
          src: `https://streamreaders.com${props.audio_url}`,
          type: "audio/mp3",
          // size: 720,
        },
        {
          src: "/path/to/movie.webm",
          type: "video/webm",
          size: 1080,
        },
      ],
    };
    player.source = sources;
    return () => {
      player.destroy();
    };
  }, [currentTime]);

  


  // if(parseInt(player.currentTime) > 10){ 

  //   useEffect(() => {
  //     player.on("loadeddata", onDataLoaded);

  //     function onDataLoaded() {
  //       setCurrentTime(() => player.currentTime)
          
  //           player.off("loadeddata",onDataLoaded);
  //           player.currentTime = 0;
          
  //     }
  //   })
    
  //   // player.on('ready', () => {setCurrentTime(() => player.currentTime
  //     // if(parseInt(currentTime) > 10 /*props.free_time && !props.isLoggedin*/){
  //     // player.pause()
  //     // player.currentTime(0)
  //     // props.displayFreeEnded()
      
    
    
  // }, [currentTime])

  
  return <audio className="js-plyr plyr"></audio>;
};



Plyr.defaultProps = {
  options: {
    controls: [
      "rewind",
      "play",
      "fast-forward",
      "progress",
      "current-time",
      "duration",
      "mute",
      "volume",
      "settings",
      "fullscreen",
    ],
    i18n: {
      restart: "Restart",
      rewind: "Rewind {seektime}s",
      play: "Play",
      pause: "Pause",
      fastForward: "Forward {seektime}s",
      seek: "Seek",
      seekLabel: "{currentTime} of {duration}",
      played: "Played",
      buffered: "Buffered",
      currentTime: "Current time",
      duration: "Duration",
      volume: "Volume",
      mute: "Mute",
      unmute: "Unmute",
      enableCaptions: "Enable captions",
      disableCaptions: "Disable captions",
      download: "Download",
      enterFullscreen: "Enter fullscreen",
      exitFullscreen: "Exit fullscreen",
      frameTitle: "Player for {title}",
      captions: "Captions",
      settings: "Settings",
      menuBack: "Go back to previous menu",
      speed: "Speed",
      normal: "Normal",
      quality: "Quality",
      loop: "Loop",
    },
  },
};

Plyr.propTypes = {
  options: PropTypes.object,
  sources: PropTypes.object,
  source: PropTypes.func,
  destroy: PropTypes.func,
};

export default Plyr;

