import React from 'react'
import IconCardSmall from './IconCardSmall'
import '../styles/iconSectionLanding.css'


function IconSectionLanding(){
    return(
        <section className="icon-section-landing">
            <h2>Únete a Nuestra Comunidad</h2>
            <div className="icon-container">
                <IconCardSmall src={require("../assets/Icono Audiolibros.png")} alt="audiolibro" text="Audiolibros con Ambiente" />
                <IconCardSmall src={require("../assets/icono auris.png")} alt="auris" text="Contenido Nuevo Todos los Meses" />
                <IconCardSmall src={require("../assets/Sonido 3D.png")} alt="sonido3d" text="Sonido 3D" />
                <IconCardSmall src={require("../assets/Icono Membresia.png")} alt="membresiar" text="Calidad Inigualable" />
                <IconCardSmall src={require("../assets/Icono Play.png")} alt="play" text="Escucha los Mejores Clásicos" />
                <IconCardSmall src={require("../assets/ICONO LEER.png")} alt="leer" text="Una Nueva Forma de Leer" />
            </div>

            
        </section>

        
    )
}

export default IconSectionLanding