import React from "react";
import "../styles/pricingSection.css";

function PricingSection() {
  return (
    <section class="price-list">
        <div class="price-cont">
            <p class="price-title">ACCESO ILIMITADO</p>
            <p class="price">U$3</p>
            <p class="price-sub">POR UN AÑO</p>
            <div class="price-item-cont">
                <div class="price-item">
                    <i>&#10004;</i>
                    <p>Escucha nuestro contenido sin restricciones</p>
                </div>
                <div class="price-item">
                    <i>&#10004;</i>
                    <p>Nuevos audiolibros todos los meses</p>
                </div>
                <div class="price-item">
                    <i>&#10004;</i>
                    <p>Aprovecha esta increíble oferta</p>
                </div>
                <div class="price-item">
                    <i>&#10004;</i>
                    <p>Promoción por tiempo limitado</p>
                </div>

            </div>
            <button class="price-btn">SUSCRIBETE AHORA</button>
        </div>
    </section>
  )
}

export default PricingSection