import React, { useEffect, useState } from "react";
import Plyr from "./AudioPlayer"

import { Link } from "react-router-dom";
import "../styles/bookPage.css";


function BookPage(props) {

  const [freeEndedClass, setFreeEndedClass] = useState('free-ended hide')
  function displayFreeEnded(){
    setFreeEndedClass('free-ended show')
  }

  return (
    <div className="book-page" >
      <div className="cover-cont">
        <img src={`https://streamreaders.com${props.image_url}`} />
       
        <p>PARA UNA MEJOR EXPERIENCIA</p>
      </div>
      <div className="text-cont">
        
        <p className="title">{props.name}</p>
        <p className="author">{props.author_name}</p>
        <p className="narrador">NARRADO POR: {props.narrator}</p>
        <p className="sinopsis">{props.sinopsis}</p>
        
      </div>
      <div className="suscribete">
        <Link className="link" to="/suscribete" className="link">
          <button className="btn">SUSCRIBETE</button>
        </Link>
        <p className="texto-suscribete">Disfruta de todo el contenido de Stream Readers sin restricciones</p>
      </div>
      <div className="player-cont">
        <Plyr
          audio_url={props.audio_url}
          free_time={props.free_time} 
          duration={props.duration}
          isLoogedin={props.isLoggedin}
          displayModal={displayFreeEnded}
        />
        <p className="gratis">¡Escucha un ADELANTO!</p>
      </div>

      <div className={freeEndedClass}>Free time ended</div>
    </div>
  );
}

export default BookPage;
