import React from "react";
import "../styles/heroElNosotros.css";

function HeroElNosotros() {
  return (
    <div class="hero-text-nosotros">
      <div class="line">
        <p class="hero-sub">
          <span>Stream Readers</span> nace a partir de nuestro amor por la lectura. En la
          actualidad, es difícil encontrar un espacio para sentarse a leer, es
          por ello que creemos que los libros también pueden ser escuchados para
          ser disfrutados. Nuestra obsesión es generar contenido de calidad
          transformando una obra literaria en una experiencia auditiva,
          utilizando tecnología de vanguardia y sin perder la esencia de la obra
          original.
        </p>
      </div>
    </div>
  );
}

export default HeroElNosotros;
